html {
  -webkit-user-select: none;
  user-select: none;
  background-image: url("../background.93c4eaf5.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
}

body {
  place-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  display: grid;
}

.foreground {
  backdrop-filter: blur(10px) brightness(.8);
  border-radius: 2em;
  place-items: center;
  padding: 50px;
}

.toolbar {
  color: #fff;
  justify-content: space-around;
  width: 100%;
  font-size: 20px;
  font-weight: bold;
  display: flex;
}

#map {
  border: 3px solid #fff;
  margin-top: 15px;
}
